<template>
  <div>
    <div v-if="loggedIn">
      <v-toolbar id="app-toolbar" app dark color="pink" 
        flat
        fixed
        scroll-off-screen
        scroll-threshold="120">
        <v-toolbar-title>CornApp Admin</v-toolbar-title>
      </v-toolbar>
      <v-content style="padding-bottom: 60px;">
        <list-lahan 
          v-if="currentView == 'list'" 
          v-on:active-change="handleActiveChange"/>
        <editor 
          v-if="currentView == 'editor'" 
          v-on:close="currentView = 'list'"
          :active-id="activeId"/>
        <rating-kriteria v-if="currentView == 'rating'"/>
        <bottom-nav v-on:change-view="handleChangeView"/>
      </v-content>
    </div>
    <div v-else>
      <v-dialog max-width=500 value=true>
        <v-card>
          <v-card-text>
            <v-text-field type="text" v-model="form.username" placeholder="Username" label="Username"/>
            <v-text-field type="password" v-model="form.password" placeholder="Password" label="Password"/>
          </v-card-text>
          <v-card-actions>
            <v-btn flat color="blue" @click="doLogin">Login</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </div>
  </div>
</template>

<script>
import ListLahan from './ListLahan'
import Editor from './Editor'
import BottomNav from './BottomNav'
import RatingKriteria from './RatingKriteria'
import Cookies from 'js-cookie'
import { login, logout } from '../services'

export default {
  name: 'MainContainer',

  components: {
    ListLahan,
    Editor,
    RatingKriteria,
    BottomNav
  },

  data () {
    return {
      currentView: 'list',
      activeId: null,
      username: null,
      userid: null,
      loggedIn: false,
      form: {
        username: '',
        password: ''
      }
    }
  },

  methods: {
    handleActiveChange (active) {
      this.activeId = active
      this.currentView = 'editor'
    },
    handleChangeView (v) {
      // Go to user space if it's "web"
      if (v === 'web') {
        window.location = '/index.html'
      } else if (v === 'log-out') {
        logout()
        this.checkSession()
      } else {
        this.currentView = v
      }
    },
    doLogin () {
      login(this.form.username, this.form.password)
      this.checkSession()
    },
    doLogout () {
      logout()
    },
    checkSession () {
      let prefix = 'yuni-fsaw'
      if (Cookies.get(`${prefix}.username`) != null) {
        this.loggedIn = true
        this.username = Cookies.get(`${prefix}.username`)
        this.password = Cookies.get(`${prefix}.password`)
      } else {
        this.loggedIn = false
      }
    }
  },

  mounted () {
    this.checkSession()
  }
}
</script>

<style>
#app-toolbar .v-toolbar__content {
  justify-content: center;
}
</style>



